$green: #008000

body
    font-family: sans-serif
.content
    width: 30vw
    min-width: 365px
    max-width: 500px
    margin: 0 auto
    text-align: center
h1
    color: red
div
    &.recipients
        p
            color: $green
            font-size: 14pt
nav
    display: flex
    color: #888888
    user-select: none
    & > span
        width: 33%
a.link, nav span:hover
    color: $green
    cursor: pointer
.clear
    clear: both
.prev
    float: left
    text-align: left
.current
    float: none
    text-align: center
.next
    float: right
    text-align: right
.disclaimer
    color: #aaaaaa
    position: fixed
    bottom: 0
    padding: 20px

.christmas-countdown
    background-color: #08b5ff
    border-radius: 10px
    color: white
    padding: 10px
    margin-top: 50px
    & .lable
        text-transform: uppercase
        letter-spacing: 5px
        font-size: 10pt
    & .time
        display: flex
        flex-direction: row
        span
            font-size: 20pt
            padding: 10px
            text-align: center
            diplay: inline-block
            flex-grow: 1
            &.days::after,
            &.hours::after,
            &.minutes::after,
            &.seconds::after
                display: inline-block
                margin-left: 0
                position: relative
                left: 5px
                font-size: 8pt
            &.days::after
                content: " days"
            &.hours::after
                content: " hours"
            &.minutes::after
                content: " minutes"
            &.seconds::after
                content: " seconds"
